        <div class="modal-container">
          <div class="modal-header">
            <slot name="header">
              <span v-if="!isEdit">Новый товар</span>
              <span v-if="isEdit">Редактирование</span>
            </slot>
          </div>

          <div class="modal-body mb-0">
            <slot name="body">
              <form>
                <label>Название</label>
                <suggestions
                  class="v-inputslot"
                  v-model="productInfo.productName"
                  :options="options"
                  options.placeholder="Название"
                  :onInputChange="onCountryInputChange"
                />
                <div class="d-flex">
                  <div class="w-70">
                    <label>Количество</label>
                    <v-text-field
                      v-model="productInfo.amount"
                      solo
                      type="number"
                    ></v-text-field>
                  </div>
                  <div class="d-flex align-center">
                    <v-combobox
                      v-model="productInfo.selectUnit"
                      :items="units"
                      label="единицы"
                      outlined
                      dense
                      class="w-30 ml-4"
                    ></v-combobox>
                  </div>
                </div>
              </form>
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer">
              <button
                class="modal-default-button btn btn-outline-secondary"
                @click="$emit('close')"
              >
                <span v-show="!isEdit">отмена</span>
                <span v-show="isEdit">закрыть</span>
              </button>
              <button
                :disabled="!productInfo.productName"
                class="modal-default-button btn btn-success"
                @click="save"
              >
                сохранить
              </button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
