<script>
export default {
  name: "Modal",
  components: {
    Suggestions: () => import("v-suggestions"),
  },
  props: {
    productInfo: {
      type: Object,
      default: function() {
        return {
          productName: "",
          amount: "",
          selectUnit: "шт",
        };
      },
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    let products = [
      "Хлеб",
      "Масло",
      "Олия",
      "Майонез",
      "Сметана",
      "Молоко",
      "Мясо курятина",
      "Сахар",
      "Соль",
      "Мука",
      "Гречка",
      "Мороженое",
      "Макароны",
      "Сыр",
      "Творог",
      "Сгущенное молоко",
      "Яйца",
      "Йогурт",
      "Рис",
      "Приправа",
      "Кетчуп",
      "Конфеты",
      "Яблоки",
      "Груши",
      "Сливы",
      "Помидоры",
      "Огурцы",
      "Перец",
      "Картофель",
      "Капуста",
      "Дрожжи",
      "Ванилин",
      "Крышки для консервации",
    ];
    return {
      countries: products,
      selectedCountry: null,
      options: {},
      loading: false,
      items: [],
      search: null,
      units: ["шт", "л", "кг", "упаков.", "г"],
    };
  },

  methods: {
    save() {
      this.$emit("save", { ...this.productInfo });
    },
    onCountryInputChange(query) {
      if (query.trim().length === 0) {
        return null;
      }
      // return the matching countries as an array
      return this.countries.filter((country) => {
        return country.toLowerCase().includes(query.toLowerCase());
      });
    },
    onCountrySelected(item) {
      this.selectedCountry = item;
    },
    onSearchItemSelected(item) {
      this.selectedSearchItem = item;
    },
  },
};
</script>

